.reg-form {
  display: flex;
  flex-direction: column;
  position: relative;
  left: 20px;
  top: 70px;
  height: 320px;
  width: 300px;
  background: #013267a0;
  padding: 90px;
  opacity: 0.89;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.reg-form h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #ffffff;
}

.reg-form input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.reg-form button {
  width: 100%;
  padding: 15px;
  background-color: #013267;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.reg-form button:hover {
  background-color: #3c7dc4;
}

.link1{
  color: #fffefe;
}

/* .input input::placeholder {
  color: #002267;
} */

.reg-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; /* Put the video behind other content */
}