.crud-container {
  margin-top: 20px;
}

.border {
  border: none;
  padding: 20px;
  border-radius: 8px;
}

.text-center {
  text-align: center;
}

.form-label {
  font-weight: bold;
}

.btn-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.btn-container button {
  margin-right: 10px;
}

.table tbody tr:hover {
  background-color: #494949;
}

.movie-form {
  padding: 20px;
  border: none;
  border-radius: 8px;
  margin: 20px auto;
  width: 50%;
}

.movie-form .form-label {
  font-weight: bold;
}

.movie-form .btn-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.btn-container .btn-cancel {
  background-color: lightcoral;
  width: 120px;
  height: 35px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.btn-container .btn-submit {
  background-color: rgb(0, 125, 167);
  width: 120px;
  height: 35px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.btn-container .btn-submit:hover {
  background-color: rgb(2, 96, 127);
}

.btn-container .btn-cancel:hover {
  background-color: rgb(188, 99, 99);
}
